@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,700&display=swap);
.header {
  height: 80px;
  font-size: 1.66667rem;
  font-weight: 700;
  box-shadow: 0 3px 15px -9px rgba(80, 90, 100, 0.6);
  color: #fff; }

.modal-wrapper {
  background-color: rgba(80, 90, 100, 0.6);
  color: #303133;
  top: 0;
  left: 0;
  z-index: 1; }
  .modal-wrapper .base-modal {
    min-width: 310px;
    background-color: #ffffff; }
    .modal-wrapper .base-modal .modal-title {
      font-size: 1.5rem; }

.task-item {
  height: 300px; }

.isDone {
  position: relative; }
  .isDone:before {
    content: '';
    display: block;
    position: absolute;
    width: 30px;
    height: 30px;
    background: url("https://img.icons8.com/color/48/000000/checked-2.png") center no-repeat;
    background-size: 30px;
    top: -10px;
    right: -10px; }

.controls-list {
  right: 0;
  top: 55px; }
  .controls-list:after {
    content: '';
    display: block;
    position: absolute;
    width: 8px;
    height: 8px;
    border-top: 1px solid #343a40;
    border-left: 1px solid #343a40;
    background-color: #f8f9fa;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    top: -5px;
    right: 15px; }

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline; }

a {
  text-decoration: none; }
  a:active, a:hover {
    outline: 0; }

ul, li {
  list-style-type: none;
  margin: 0;
  padding: 0; }

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal; }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: border-box; }

:focus {
  outline: 0; }

img, audio, video {
  max-width: 100%;
  height: auto; }

audio, canvas, iframe, video, img, svg {
  vertical-align: middle; }

iframe {
  border: 0; }

textarea {
  resize: none;
  /*remove the resize handle on the bottom right*/
  overflow: auto;
  vertical-align: top;
  box-shadow: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none; }

input, textarea, select, button {
  outline: none;
  border: none;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td, th {
  padding: 0;
  text-align: left; }

html {
  line-height: 1.4;
  font-size: 18px;
  color: #fff;
  height: 100%; }
  @media screen and (max-width: 1280px) {
    html {
      font-size: 16px; } }
  @media screen and (max-width: 768px) {
    html {
      font-size: 15px; } }
  @media screen and (max-width: 480px) {
    html {
      font-size: 14px; } }

body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", Helvetica, Arial, sans-serif;
  font-size: 18px;
  font-weight: 400;
  color: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #2b2b2b; }

*, *:before, *:after {
  box-sizing: border-box;
  outline: none !important; }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

a {
  text-decoration: none; }

ul, li {
  margin: 0;
  padding: 0;
  list-style-type: none; }

code {
  font-size: 12px; }

section, .section {
  position: relative;
  padding: 40px 0; }
  @media screen and (max-width: 1280px) {
    section, .section {
      padding: 36px 0; } }
  @media screen and (max-width: 1280px) {
    section, .section {
      padding: 28px 0; } }
  @media screen and (max-width: 1280px) {
    section, .section {
      padding: 20px 0; } }

